import React from 'react';
import { Router, Switch, Route } from 'react-router-dom';
import history from './services/history';
import CadastroUsuario from './views/registerUser';
import Home from './views/admin/home';
import HomeClient from './views/client/home';
import RegisterProducts from './views/admin/registerProducts';
import RegisterUser from './views/admin/registerUser';
import Report from './views/admin/report';
import ShowUsers from './views/admin/showUsers';
import ShowProducts from './views/admin/showProducts';
import Login from './views/login';
import 'bootswatch/dist/minty/bootstrap.min.css';
import ListCart from './views/client/ListCart';
import Checkout from './views/client/checkout';
import ProductDetails from '../src/views/client/productDetail';

function App() {
  return (
    <div className="App">
      <Router history={ history }>
        <Switch>
          <Route exact path={`${process.env.PUBLIC_URL}/admin/home`} component={ Home } />
          <Route exact path={`${process.env.PUBLIC_URL}/admin/cadastrarusuarios`} component={ RegisterUser } />
          <Route exact path={`${process.env.PUBLIC_URL}/admin/cadastrarprodutos`} component={ RegisterProducts } />
          <Route exact path={`${process.env.PUBLIC_URL}/admin/listarusuarios`} component={ ShowUsers } />
          <Route exact path={`${process.env.PUBLIC_URL}/admin/listarprodutos`} component={ ShowProducts } />
          <Route exact path={`${process.env.PUBLIC_URL}/admin/relatorios`} component={ Report } />
          <Route exact path={`${process.env.PUBLIC_URL}/home`} component={ HomeClient } />
          <Route exact path={`${process.env.PUBLIC_URL}/cadastrarusuarios`} component={ CadastroUsuario } />
          <Route exact path={`${process.env.PUBLIC_URL}/login`} component={ Login } />
          <Route exact path={`${process.env.PUBLIC_URL}/`} render={ () => history.push('/login') } />
          <Route path={`${process.env.PUBLIC_URL}/minhaListaDeProdutos`} component={ ListCart } />
          <Route path={`${process.env.PUBLIC_URL}/carrinho`} component={ Checkout } />
          <Route path={`${process.env.PUBLIC_URL}/detalhesProduto/:id`} render={ (props) => <ProductDetails { ...props } /> } />
        </Switch>
      </Router>
    </div>
  );
}

export default App;
